html, body {
    min-height: 100%;
    height: 100%;
    font: 14px "Century Gothic", Futura, sans-serif;
    background-color: rgb(32, 32, 32);
    color: rgb(201, 201, 201);
  }
.NumberLabel{
    float: left;
    text-align: left;
}
.CharacterPicker{
    font: 20px "Century Gothic", Futura, sans-serif;
}
.Stat{
    padding: 15px;
}
ul {
    list-style-type: none;
    padding:0;
    margin:0;
}
li { background: rgba(31, 31, 31, 0.5); 
    min-width:650px;
    padding: 0;
    margin: 0;
    text-align:left;

    font: 18px "Century Gothic", Futura, sans-serif;}
li:nth-child(odd) { background: rgba(77, 77, 77, 0.5); }
.StatWrapper:after {
        clear: both;
        content: "";
        display: table;
    }
.StatWrapper{
    text-align: left;
    padding:0;
    margin: 0;
}
.StatName{
    width: 46%;
    float: left;
    text-align: right;
    padding-right:8%;
    margin:0;
}
.StatValue{   
    float:left;
    text-align: left;
    margin:0;
}
.EntryBox{
    float: right;
    text-align: right;
    width: 60px;
    margin-left: 20px;
}
.BuildInfo {
    text-align: center;
}
.title {
    font: 50px "Century Gothic", Futura, sans-serif;
    margin: none;
    padding: none;
}
.PrimarySection {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.BoxInfo {
    border-style: solid;
    border-width: 3px;
    border-color: rgb(62, 69, 86);
    background-color: rgba(53, 61, 75); 
    border-radius: 8px;
    padding: 5px;
    margin: 15px;
}
input, select, textarea{
    color: rgb(201, 201, 201);
    border-style: solid;
    border-width: 3px;
    border-color: rgb(43, 47, 58);
    background-color: rgb(74, 86, 105); 
  }
.GeneratedUrl{
    word-wrap: break-word;
    float:right;
    font: 10px "Century Gothic", Futura, sans-serif;
}
.SectionHeader {
    font: 60px "Century Gothic", Futura, sans-serif;
}
.subtitle {
    font: 30px "Century Gothic", Futura, sans-serif;
}
.subtitle2 {
    font: 20px "Century Gothic", Futura, sans-serif;
}
.UrlGenerator{
    max-width: 700px;
    min-width: 500px;
    
    text-align: left;
}
.InfoCollumn {
    width: 300px;
    float:left;
    padding: 10px;
}
.Loadout-body {
    padding: 20px;
    height:100%;
    max-width:960px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    background-color: rgb(35, 35, 36); 
}
button{
    border-color:  rgb(74, 86, 105);
    background-color:rgb(43, 47, 58); 
    margin: 5px;
    padding: 5px;
    color: rgb(201, 201, 201);
}